.main-header {
  background: linear-gradient(rgba(255, 255, 255, 0.8), #ffffff), linear-gradient(90deg, #ff5c4d, #eb469f, #8341ef, #3f68f9);
}
.main-header__wrapper {
  padding: 32px 32px 0;
}
.main-header__logo-yandex {
  display: block;
  margin-bottom: 32px;
}
.main-header h1 {
  font-weight: 900;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
  margin-bottom: 35px;
}
.main-header__text {
  font-weight: 500;
  font-size: 18px;
  margin: 0;
}
.main-header__price {
  display: flex;
  align-items: center;
  width: 102px;
  background: radial-gradient(rgba(241, 241, 241, 0.6), rgba(241, 241, 241, 0.6)), radial-gradient(#E0B8FF, #B6B9FF);
  border-radius: 500px;
  padding-right: 5px;
}
.main-header__new-price {
  display: block;
  background: linear-gradient(40deg, #b6b9ff, #e0b8ff);
  border-radius: 500px;
  padding: 5px 8px 5px 7px;
}
.main-header__old-price {
  margin-left: 4px;
  color: #808080;
}
.main-header__button {
  display: block;
  padding: 18px 72px;
  width: 311px;
  margin: 0 auto;
}
.main-header__button-wrapper {
  width: 100%;
  position: fixed;
  bottom: 24px;
}
.button {
  background-color: #FFDE40;
  border: none;
  border-radius: 36px;
  color: #21201F;
  font-size: 18px;
  font-weight: 500;
}
.advantages {
  padding: 0 32px;
  margin-bottom: 32px;
}
.advantages h2 {
  margin: 0;
}
.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding-top: 32px;
}
.advantages__item {
  display: flex;
  justify-content: space-between;
  padding: 22.5px 13px;
}
.advantages__item svg {
  width: 83px;
  height: 83px;
}
.advantages__text {
  display: flex;
  align-items: center;
  width: 212px;
  margin-left: 16px;
}
.exclusive {
  margin-bottom: 72px;
}
.exclusive__wrapper {
  padding: 0 32px;
}
.exclusive__gallery {
  width: 343px;
  margin: 0 auto;
}
.photo-gallery__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 24px;
  position: relative;
}
.photo-gallery__list::after {
  content: url("../../img/gallery-icon.png");
  position: absolute;
  top: -80px;
  right: -20px;
}
.photo-gallery__item {
  display: none;
}
.photo-gallery__item--active {
  display: block;
}
.photo-gallery__image {
  display: block;
  border-radius: 48px;
}
.photo-gallery__carusel-button {
  width: 108px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.photo-gallery__button {
  background-color: #FFDE40;
  border-radius: 50%;
  border: none;
  padding: 0;
  width: 48px;
  height: 48px;
  cursor: pointer;
}
.photo-gallery__button:disabled {
  opacity: 30%;
}
.photo-gallery__button:hover {
  background-color: #e9ca31;
}
.accede h2 {
  margin: 0;
  margin-bottom: 32px;
}
.accede__wrapper {
  padding: 0 32px;
  margin-bottom: 4px;
}
.accede__text {
  margin: 0;
}
.accede img {
  display: block;
  margin: 0 auto;
  object-fit: cover;
  background-position: 0 0;
  background-size: cover;
}
.subscription {
  padding: 0 32px;
  margin-bottom: 72px;
}
.subscription h2 {
  margin: 0;
  margin-bottom: 32px;
}
.subscription__text {
  margin: 0;
  margin-bottom: 32px;
}
.subscription__button {
  padding: 18px 86px;
  filter: drop-shadow(0 4px 32px rgba(255, 222, 64, 0.59));
}
.main-footer {
  padding: 0 32px 72px;
}
.main-footer__text {
  font-size: 10px;
  color: #C5C5C5;
  margin: 0;
}
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'text-black';
  color: #21201F;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
}
@font-face {
  font-family: 'text-regular', sans-serif;
  src: url("../fonts/text-regular.woff") format("woff");
}
@font-face {
  font-family: 'text-medium', sans-serif;
  src: url("../../fonts/text-medium.woff") format("woff");
}
@font-face {
  font-family: "text-black", sans-serif;
  src: url("../../fonts/text-regular.woff") format("woff");
}
