.advantages {
    padding: 0 32px;
    margin-bottom: 32px;
}

.advantages h2 {
    margin: 0;
}

.advantages__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    padding-top: 32px;
}

.advantages__item {
    display: flex;
    justify-content: space-between;
    padding: 22.5px 13px;
}

.advantages__item svg {
    width: 83px;
    height: 83px;
}

.advantages__text {
    display: flex;
    align-items: center;
    width: 212px;
    margin-left: 16px;
}