.photo-gallery__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    margin-bottom: 24px;
    position: relative;
}

.photo-gallery__list::after {
    content: url("../../img/gallery-icon.png");
    position: absolute;
    top: -80px;
    right: -20px;
}

.photo-gallery__item {
    display: none;

    &--active {
        display: block;
    }
}

.photo-gallery__image {
    display: block;
    border-radius: 48px;
}

.photo-gallery__carusel-button {
    width: 108px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.photo-gallery__button {
    background-color: #FFDE40;
    border-radius: 50%;
    border: none;
    padding: 0;
    width: 48px;
    height: 48px;
    cursor: pointer;

    &:disabled {
        opacity: 30%;
    }

    &:hover {
        background-color: #e9ca31;
    }
}