.main-header {
    background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 1)),
     linear-gradient(90deg, rgba(255, 92, 77, 1), rgba(235, 70, 159, 1), rgba(131, 65, 239, 1), rgba(63, 104, 249, 1));
}
.main-header__wrapper {
    padding: 32px 32px 0;
}

.main-header__logo-yandex {
    display: block;
    margin-bottom: 32px;
}

.main-header h1 {
    font-weight: 900;
    font-size: 36px;
    line-height: 36px;
    margin: 0;
    margin-bottom: 35px;
}

.main-header__text {
    font-weight: 500;
    font-size: 18px;
    margin: 0;
}

.main-header__price {
    display: flex;
    align-items: center;
    width: 102px;
    background: radial-gradient(rgba(241, 241, 241, 0.6), rgba(241, 241, 241, 0.6)), radial-gradient(#E0B8FF, #B6B9FF);
    border-radius: 500px;
    padding-right: 5px;
}

.main-header__new-price {
    display: block;
    background: linear-gradient(40deg, rgba(182, 185, 255, 1), rgba(224, 184, 255, 1));
    border-radius: 500px;
    padding: 5px 8px 5px 7px;
}

.main-header__old-price {
    margin-left: 4px;
    color: #808080;
}

.main-header__button {
    display: block;
    padding: 18px 72px;
    width: 311px;
    margin: 0 auto;
}

.main-header__button-wrapper {
    width: 100%;
    position: fixed;
    bottom: 24px;
}
